<template>
	<div id="language">
		<public-header title="语言设置"></public-header>
		<div class="content">
			<van-cell-group title="点击切换语言">
				<template v-for="(item, index) in languages">
					<van-cell :title="item.title" :value="item.key == language ? '当前设置' : ''" @click="onToggleLanguage(index)" clickable is-link />
				</template>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {
				language: 'zh_jian',
				languages: [],
				showPreLoading: true
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'wmall/member/profile/language'
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					this.languages = [...result.message.languages];
					var language = this.util.getStorage('ilanguage');
					this.language = language ? language : this.language;
				})
			},

			onToggleLanguage(index) {
				var language = this.languages[index];
				if(language.key == this.language) {
					return false;
				}
				this.$dialog.confirm({
					message: '确定将语言设置为' + language['title'] + '吗?'
				}).then(() => {
					this.util.setStorage('ilanguage', language.key);
					var url = 'back';
					if(language.url) {
						url = language.url;
					}
					this.util.$toast('设置成功', url);
				}).catch(() => {

				})
			}
		},

		mounted() {
			this.onLoad();
		}
	}
</script>

<style>

</style>


